import React from 'react';
import ReactDOM from 'react-dom';
import Instascan from 'instascan-v2';
import {showAlert} from '../Alert';
import SubmitDataHelpers from '../helpers/SubmitDataHelpers';
import CookieHelpers from '../helpers/CookieHelpers';

import ScanStyle from '../../css/guest/ScanStyle';
import '../../css/guest/Scan.css';

class Scan extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            score: 0,
            checkPointId: -1,
            scanNo: 0,
        };

        this.handleResult = this.handleResult.bind(this);
        this.countTimeLeft = this.countTimeLeft.bind(this);
    }

    componentDidMount(){
        let cookie = new CookieHelpers;
        let loggedId = cookie.getCookie("username");

        let guest = window.localStorage.getItem("guest");
        
        if(!loggedId && !(guest && guest.indexOf("-") > 0)){
            window.alert("Login or access as a guest to start scanning...");
            window.location.href = "/";
            return false;
        }

        this.setState({
            score: 0,
            checkPointId: -1,
            scanNo: 0,
        });

        var self = this;
        if(document.getElementById('preview')){
            let scanner = new Instascan.Scanner({ video: document.getElementById('preview'), scanPeriod: 5, mirror: false, });
                scanner.addListener('scan', function (content) {
                    self.setState({...self.state, ['scanText']: content});
                    self.handleResult();
                });

            Instascan.Camera.getCameras().then(function (cameras) {
                if (cameras.length > 0) {
                    scanner.start(cameras[0]);
                } else {
                    window.alert("No camera found");
                }
            });
        }

        let countTimeLeft = window.setInterval(this.countTimeLeft,1000);
        window.sessionStorage.setItem('countTimeLeft', countTimeLeft);
        ScanStyle();
    }

    componentDidUpdate(){
        if(document.getElementById('scanResults')){
            this.renderResult();
        }

        ScanStyle();
    }

    componentWillUnmount(){
        clearInterval(window.sessionStorage.countTimeLeft);
    }

    render(){
        return (
            <div id='scan' className='scan'>
                <video id="preview"></video>
                <div id="scanResults" className="scanResults col-8 col-sm-6 mx-auto justify-content-center py-4 shadow">
                    <h4 className='col-12 mb-3'>Result</h4>
                    <div className='col-10 d-flex flex-row justify-content-center mx-auto p-3 shadow'>
                        <div className='px-1'>
                            <div className='text-left'>Checkpoint No</div>
                            <div className='text-left'>Number of Scans</div>
                            <div className='text-left'>Time Taken</div>
                            <div className='text-left'>{this.state.course == 'Score'? 'Score' : ''}</div>
                            <div className='text-left'>Position</div>
                            <div className='text-left'>Time Left</div>
                        </div>
                        <div id='result' className='result px-1'></div>
                    </div>
                </div>
            </div>
        );
    }

    handleResult(){
        let date = new Date();
        let today = date.getFullYear() + '-' + (date.getMonth()<9? '0' + (date.getMonth()+1): date.getMonth() + 1) + '-' + date.getDate();
        
        let h = date.getHours() < 10? '0' + date.getHours() : date.getHours();
        let m = date.getMinutes() < 10? '0' + date.getMinutes() : date.getMinutes();
        let currentTime = h + ":" + m;

        let scanText = this.state.scanText.slice().split("-");
        if(scanText.length < 6){
            showAlert("Invalid QRCode", 5000);

            this.setState({error: "Invalid QRCode"});
            return false;
        }

        if(scanText.length > 5 && this.state.checkPointId == -1){
            let firstCheckPoint = scanText[3];

            this.setState({
                firstCheckPoint: firstCheckPoint,
                checkPointId: (firstCheckPoint - 1)
            });
        }
        
        let cookie = new CookieHelpers;
        let helpers = new SubmitDataHelpers;

        helpers.postData('/results/addGuestResult/', 
        {
            today: today,
            currentTime: currentTime,
            username: cookie.getCookie("username")? cookie.getCookie("username") : window.localStorage.getItem("guest"),
            scanText: this.state.scanText,
            checkPointId: this.state.checkPointId,
            score: this.state.score,
            timeTaken: this.calculateTimeTaken(),
            scanNo: this.state.scanNo,
        }).then((res) => {
            console.log(res); // JSON data parsed by `response.json()` call
            if(res.error){
                console.log(res.error);

                if(res.endTime){
                    window.sessionStorage.setItem("event", scanText[1]);
                    window.sessionStorage.setItem("course", scanText[2]);
                    window.sessionStorage.setItem("checkpointNo", scanText[4]);
                    window.location.href = '/guest/result';
                }
                
                showAlert(res.error, 5000);

                this.setState({error: res.error});
                console.log(this.state);
            } else {
                console.log(res);
                if(res.startTime){
                    if(res.endTime < currentTime){
                        window.location.href = 'guest/result';
                    } else {
                        let endTime = res.endTime.split(":");
                        endTime = endTime[0]*3600 + endTime[1]*60;
                        currentTime = date.getHours()*3600 + date.getMinutes()*60;

                        setTimeout(function(){
                            window.location.href = 'guest/result';
                        }, (endTime - currentTime) * 1000);
                    }
                    
                    this.setState({
                        startTime: res.startTime,
                        endTime: res.endTime,
                    })
                }

                let h = parseInt(res.timeTaken/3600);
                let m = parseInt((res.timeTaken - h*3600)/60);
                let s = res.timeTaken - h * 3600 - m * 60;

                h = h < 10? '0' + h : h;
                h = h == 0? '' : h + ' h ';
                m = m < 10? '0' + m + ' m ': m + ' m ';
                s = s < 10? '0' + s + ' s': s + ' s';

                this.setState({
                    checkPointId: res.checkPointId,
                    score: res.score,
                    timeTaken: (h + m + s),
                    scanNo: res.scanNo,
                    position: res.position,
                }); 
                //window.location.reload();
                console.log(this.state);
            }
        });
    }
    
    calculateTimeTaken(){
        if(this.state.scanNo != "0"){
            let startTime = this.state.startTime;
            startTime = startTime.split(":");
            startTime = startTime[0]*3600 + startTime[1]*60;
            let date = new Date();
            let currentTime =  date.getHours()*3600 + date.getMinutes()*60 + date.getSeconds();

            return (currentTime - startTime);
        } else {
            return -1;
        }
    }

    countTimeLeft(){
        if(this.state.scanNo > 0){
            let endTime = this.state.endTime;

            endTime = endTime.split(":");
            endTime = endTime[0]*3600 + endTime[1]*60;
            
            let date = new Date();
            let currentTime =  date.getHours()*3600 + date.getMinutes()*60 + date.getSeconds();
        
            let timeLeft = endTime - currentTime;

            let h = parseInt(timeLeft/3600);
            let m = parseInt((timeLeft - h*3600)/60);
            let s = timeLeft - h * 3600 - m * 60;

            h = h < 10? '0' + h : h;
            h = h == 0? '' : h + ' : ';
            m = m < 10? '0' + m + ' : ': m + ' : ';
            s = s < 10? '0' + s : s;
            
            timeLeft = h + m + s;

            this.setState({timeLeft: timeLeft});
        }
    }

    renderResult(){
        let resultComponent = [];

        if(this.state.scanNo > 0){
            resultComponent.push(
                <div key='result'>
                    <div className='text-left'>: {this.state.checkPointId - this.state.firstCheckPoint + 1}</div>
                    <div className='text-left'>: {this.state.scanNo}</div>
                    <div className='text-left'>: {this.state.timeTaken}</div>
                    <div className='text-left'>{this.state.course == 'Score'? (': ' + this.state.score) : ''}</div>
                    <div className='text-left'>: {this.state.position}</div>
                    <div className='text-left'>: {this.state.timeLeft}</div>
                </div>
            );
        } else {
            resultComponent.push(
                <div key='result'>
                    <div>: --</div>
                    <div>: --</div>
                    <div>: --</div>
                    <div>{this.state.course == 'Score'? ': --' : ''}</div>
                    <div>: --</div>
                    <div>: --</div>
                </div>
            );
        }

        ReactDOM.render(resultComponent, document.getElementById('result'));
    }
}

export default Scan;